@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Poppins:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #282c34;
  color: #fff; }

h1 {
  font-family: "Bebas Neue";
  font-size: 52px;
  letter-spacing: 0.1rem; }
  h1:hover {
    color: #09d3ac; }

p {
  font-family: "Poppins";
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  margin-top: 200px;
  width: 340px; }
  @media screen and (max-width: 400px) {
    p {
      margin-top: 0;
      width: 100%; } }

a {
  color: #1f2227;
  margin: 0 10px;
  text-decoration: none; }
  a.active, a:hover {
    color: #09d3ac; }

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

header,
footer {
  color: #1f2227;
  align-items: center;
  background: #fff;
  display: flex;
  flex-shrink: 0;
  font-family: "Poppins";
  font-size: 15px;
  font-weight: 600;
  height: 60px;
  justify-content: flex-end;
  width: 100%; }

footer {
  justify-content: center; }

main {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  position: relative; }
  main .page {
    position: absolute; }
    main .page .inner {
      display: flex;
      justify-content: center; }
      @media screen and (max-width: 400px) {
        main .page .inner {
          flex-direction: column;
          padding: 0 5%; } }

.line-wrap {
  height: 66px;
  overflow: hidden; }

.page-enter {
  opacity: 0; }

.page-enter-active {
  opacity: 1;
  -webkit-transition: opacity 400ms;
  transition: opacity 400ms;
  -webkit-transition-delay: 600ms;
          transition-delay: 600ms; }

.page-exit {
  opacity: 1; }

.page-exit-active {
  opacity: 0;
  -webkit-transition: opacity 400ms;
  transition: opacity 400ms; }

